.woocommerce-checkout.processing > .blockUI.blockOverlay {
    background-image:url('/wp-content/themes/niku-child/images/spinner.svg')!important;
    background-position: center center!important;
    background-repeat: no-repeat!important;
}

.fake-checkout-button{
    display: none;
}

#delivery_hour_on_field .woocommerce-input-wrapper label{
    float: left;
    width: 95%;
}

#delivery_hour_on_field .woocommerce-input-wrapper input{
    float: left;
    width: 15px;
    margin-top: 6px;
    margin-right: 5px;
}

.woocommerce-checkout-payment{
    margin: 25px 0 0;
}

.woocommerce-NoticeGroup-updateOrderReview, .woocommerce-message{
    font-size: 15px;
    color: red;
    list-style-type: none;
    text-align: center;
}

.back-to-shop a {
    cursor: pointer;
}

.errors-box ul{
    margin: 0;
}

.terms-and-conditions-info {
    font-weight: bold;
}

@media( max-width: 767px ) {

    .woocommerce-checkout.processing > .blockUI.blockOverlay {
        background-position: bottom center!important;
    }

    .woocommerce-checkout-review-order > p{
        line-height: 18px;
    }

    .woocommerce-checkout-review-order > p label{
        font-size: 13px;
    }

    .woocommerce-checkout-review-order > p label .input-checkbox{
        margin: -2px 2px 0 0;
    }

    #delivery_hour_on_field .woocommerce-input-wrapper label{
        width: 94%;
    }

}

@media( max-width: 420px ) {
    #delivery_hour_on_field .woocommerce-input-wrapper label{
        width: 90%;
    }
}